import Link from 'gatsby-link'

import React from 'react'
import { Container } from 'react-responsive-grid'
import Headroom from 'react-headroom'
import Favicon from '../components/favicon.js'
import Wrap from '../components/wrap'

module.exports = React.createClass({
  propTypes() {
    return {
      children: React.PropTypes.any,
      siteTitle: React.PropTypes.string,
    }
  },

  render() {
    let headerStyle = {
      transition: 'background-color 0.3s ease',
    }
    Favicon()
    if (typeof window != 'undefined') {
      if (window.location.pathname !== '/') {
      }
    }
    return (
      <div>

          <Container
            style={{
              maxWidth: 960,
              paddingTop: 0,
            }}
          >
<Wrap n={3}>
            <Link
              to={'/'}
              style={{
                color: 'black',
                textDecoration: 'none',
              }}
            >
              max bittker
            </Link>
</Wrap>
<Wrap n={3}>
            <Link
              to={'/about/'}
              style={{
                color: 'black',
                textDecoration: 'none',
              }}
            >
              about
            </Link>
</Wrap>

<Wrap n={3}>
            <Link
              to={'/games/'}
              style={{
                color: 'black',
                textDecoration: 'none',
              }}
            >
              games
            </Link>
</Wrap>
<Wrap n={3}>
            <Link
              to={'/art/'}
              style={{
                color: 'black',
                textDecoration: 'none',
              }}
            >
              art
            </Link>
</Wrap>
<Wrap n={3}>
            <Link
              to={'/blog/'}
              style={{
                color: 'black',
                textDecoration: 'none',
              }}
            >
              blog
            </Link>
</Wrap>
          </Container>
      </div>
    )
  },
})



