import React, {PropTypes} from 'react';

const wStyle = {
  border: 'black 1px solid',
  padding: '5px',
  display: 'inline-block',
  // borderBottom: 'none'
};
const Wrap = React.createClass({
  render() {
    let {n, children} = this.props;
    if (n === 0) {
      return <div style={wStyle}>{children}</div>;
    }
    return (<span style = {wStyle}><Wrap n = {n - 1}>{children}</Wrap></span>);
  }
});
export default Wrap;


